/*
* .
* @method soryBy
* @static
* @param {array} arr 
* @param {string|function} prop 
* @param {boolean} desc 
* @return {array} 
*/

export function sortBy(arr, prop, desc) {  
  let props=[],
  ret=[],
  i=0,
  len=arr.length;
  if(typeof prop=='string') {
      for(; i<len; i++){
        let oI = arr[i];
          (props[i] = new String(oI && oI[prop] || ''))._obj = oI;
      }
  }
  else if(typeof prop=='function') {
      for(; i<len; i++){
        let oI = arr[i];
          (props[i] = new String(oI && prop(oI) || ''))._obj = oI;
      }
  }
  else {
      throw '';
  }
  props.sort();
  for(i=0; i<len; i++) {
      ret[i] = props[i]._obj;
  }
  if(desc) ret.reverse();
  return ret;
};


